"use client";

import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { useRouter } from "next/navigation";
import { Spacer } from "@/components/ui/spacer";
import { useEffect, useState } from "react";
import Image from "next/image";

type HistoryItem = {
  id: string;
  imageUrl: string;
  summary: string;
};

export default function HistoryPage() {
  const router = useRouter();
  const [historyItems, setHistoryItems] = useState<HistoryItem[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchHistory() {
      try {
        const res = await fetch("/api/getHistory");
        const data = await res.json();
        setHistoryItems(data.items);
      } catch (error) {
        console.error("Error fetching history:", error);
      } finally {
        setLoading(false);
      }
    }

    fetchHistory();
  }, []);

  return (
    <div className="flex flex-col items-center justify-start min-h-screen bg-background text-foreground p-8 relative">
      <Tabs
        defaultValue="history"
        className="w-full max-w-2xl mb-8"
        onValueChange={(value) => {
          if (value === "upload") router.push("/upload");
        }}
      >
        <TabsList>
          <TabsTrigger value="history">View History</TabsTrigger>
          <TabsTrigger value="upload">Upload Images</TabsTrigger>
        </TabsList>
      </Tabs>

      <Spacer axis="vertical" size={50} />

      <h1 className="text-4xl font-bold mb-4">Prompt History</h1>
      <p className="text-lg mb-8">
        View your previous prompts and responses from the chatbot.
      </p>

      {loading ? (
        <p>Loading...</p>
      ) : historyItems.length === 0 ? (
        <p className="text-right">No history available yet...</p>
      ) : (
        <div className="grid grid-cols-1 gap-8 w-full max-w-2xl">
          {historyItems.map((item) => (
            <div key={item.id} className="bg-card p-4 rounded-lg shadow-md">
              <Image
                src={item.imageUrl}
                alt="Uploaded Image"
                width={400}
                height={300}
                className="rounded mb-4 object-cover"
              />
              <p className="text-sm">{item.summary}</p>
            </div>
          ))}
        </div>
      )}

      <p className="text-xs absolute bottom-2 right-2">
        Powered by NeuroAccess
      </p>
    </div>
  );
}
